import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Gapless5 } from '@regosen/gapless-5';
import { Pad } from './Pad';
import { reorderTracks } from '../store/actions/loop.actions';

export const PadList = ({ actx }) => {
  const { tracks, audioTracks } = useSelector((state) => state.loopModule);
  const dispatch = useDispatch();

  useEffect(() => {}, []);

  const onDragEnd = (result) => {
    // dropped outside the list
    if (!result.destination) {
      return;
    }
    dispatch(
      reorderTracks(tracks, result.source.index, result.destination.index)
    );
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId="tracks">
        {(provided, snapshot) => (
          <div
            className="pad-list main-container"
            {...provided.droppableProps}
            ref={provided.innerRef}
          >
            {tracks.map((track, index) => {
              return (
                <Draggable key={track.id} draggableId={track.id} index={index}>
                  {(provided, snapshot) => (
                    <div ref={provided.innerRef} {...provided.draggableProps}>
                      <Pad track={track} provided={provided} actx={actx} />
                    </div>
                  )}
                </Draggable>
              );
            })}
          </div>
        )}
      </Droppable>
    </DragDropContext>
  );
};
