import { tracks } from '../../constants/tracks'
const initialState = {
    actx: null,
    tracks: tracks,
    audioTracks: [],
    isPlay: false,
    isRecord: false,
    isLoop: true,
    currentTime: 0
}
export function loopReducer(state = initialState, action) {
    var newState = state;
    let tracks;
    switch (action.type) {
        // Audio Context
        case 'SET_ACTX':
            return { ...state, actx: action.actx }

        // Tracks
        case 'TOGGLE_TRACK_ACTIVE':
            return { ...state, tracks: action.tracks }
        case 'UPDATED_TRACK':
            tracks = state.tracks.map(track => (track.id === action.track.id) ? action.track : track)
            return { ...state, tracks }
        case 'UPDATED_TRACKS':
            return { ...state, tracks: action.tracks }

        // Audio Tracks
        case 'ADD_AUDIOTRACK':
            console.log('action', action)
            return { ...state, audioTracks: [...state.audioTracks, action.audioTrack] }

        // Audio State
        case 'TOGGLE_PLAY':
            return { ...state, isPlay: !state.isPlay }
        case 'TOGGLE_LOOP':
            return { ...state, isLoop: !state.isLoop }
        default:
            return newState
    }
}
