import { AppControls } from '../components/AppControls';
import { PadList } from '../components/PadList';
import { setAudioContext } from '../store/actions/loop.actions';

const AudioContext = window.AudioContext || window.webkitAudioContext;

export const Home = () => {
  const dispatch = useDispatch();
  const [actx, setAudioContext] = useState(new AudioContext());
  useEffect(() => {
    // dispatch(setAudioContext(actx));
  }, []);
  return (
    <>
      <PadList actx={actx} />
      <AppControls />
    </>
  );
};
