import { useDispatch, useSelector } from 'react-redux';
import { togglePlay, toggleLoop } from '../store/actions/loop.actions';
import { ReactComponent as Loop } from '../../assets/img/loop.svg';
import { ReactComponent as Play } from '../../assets/img/play.svg';
import { ReactComponent as Stop } from '../../assets/img/stop.svg';
import { ReactComponent as Pause } from '../../assets/img/pause.svg';
import { useEffect, useState } from 'react';

export const AppControls = () => {
  const dispatch = useDispatch();
  const { actx, isLoop, tracks, audioTracks } = useSelector(
    (state) => state.loopModule
  );
  const [isPlay, setIsPlay] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);

  useEffect(() => {
    let interval = setInterval(() => {
      // setCurrentTime((audioTracks[1]?.currentPosition() / 100) * 1.25);
    }, 50);
    return () => {
      clearInterval(interval);
    };
  }, []);

  const playPause = () => {
    if (actx.state === 'suspended') {
      actx.resume();
      tracks.forEach((track) => {
        console.log('audioTracks', audioTracks);
        if (!isPlay) audioTracks[track.id].absn.start();
        else audioTracks[track.id].stop();
      });
    }
    setIsPlay(!isPlay);
    // dispatch(togglePlay());
  };

  const stop = async () => {
    if (isPlay) {
      setIsPlay(!isPlay);
      // dispatch(togglePlay());
    }
    if (actx.state === 'running') {
      await actx.suspend();
    }
    // tracks.forEach((track) => {
    //   audioTracks[track.id].current.stop();
    //   audioTracks[track.id].current.currentTime = 0;
    // });
  };

  const loop = () => {
    tracks.forEach((track) => {
      audioTracks[track.id].current.loop = !isLoop;
    });
    dispatch(toggleLoop());
  };

  return (
    <div className="controls flex column auto-center">
      <div className="top flex">
        <button onClick={loop}>
          <Loop data-loop={isLoop} />
        </button>
        <button onClick={playPause}>{isPlay ? <Pause /> : <Play />}</button>
        <button onClick={stop}>
          <Stop />
        </button>
      </div>
      <div className="slider">
        <input
          type="range"
          min="0"
          max="100"
          value={currentTime.toString()}
          onChange={(e) => {}}
        />
        <progress min="0" max="100" value={currentTime.toString()} />
      </div>
    </div>
  );
};
