import { loopService } from "../../services/loop.service";

export const toggleTrackActive = (track) => {
    return dispatch => {
        const returnedTrack = loopService.toggleActive(track)
        dispatch({ type: 'UPDATED_TRACK', track: returnedTrack });
    }
}

export const switchTrack = (track) => {
    return dispatch => {
        const returnedTrack = loopService.switchTrack(track)
        dispatch({ type: 'UPDATED_TRACK', track: returnedTrack });
    }
}

export const reorderTracks = (tracks, startIndex, endIndex) => {
    return dispatch => {
        const returnedTracks = loopService.reorder(tracks, startIndex, endIndex)
        dispatch({ type: 'UPDATED_TRACKS', tracks: returnedTracks });
    }
}

export const addAudioTrack = (audioTrack) => {
    return dispatch => {
        dispatch({ type: 'ADD_AUDIOTRACK', audioTrack });
    }
}

// SET REDUCERS
export const setTrackVolume = (track) => {
    return dispatch => {
        dispatch({ type: 'UPDATED_TRACK', track });
    }
}

export const setAudioContext = (actx) => {
    return dispatch => {
        dispatch({ type: 'SET_ACTX', actx });
    }
}

export const togglePlay = () => {
    return dispatch => {
        dispatch({ type: 'TOGGLE_PLAY' });
    }
}

export const toggleLoop = () => {
    return dispatch => {
        dispatch({ type: 'TOGGLE_LOOP' });
    }
}

