import { useEffect, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import {
  setTrackVolume,
  toggleTrackActive,
  switchTrack,
  addAudioTrack,
} from '../store/actions/loop.actions';
import { Toggle } from './Toggle';
import { useSelector } from 'react-redux';
import { Slider } from './Slider';
import { ReactComponent as Replace } from '../../assets/img/replace.svg';
import { Gapless5 } from '@regosen/gapless-5';

export const Pad = ({ track, provided, actx }) => {
  const dispatch = useDispatch();
  const { audioTracks } = useSelector((state) => state.loopModule);
  const [gainNode, setGain] = useState(actx?.createGain());
  const audioRef = useRef(new Audio(track.path[track.currPlay]));
  const intervalRef = useRef();
  const isReady = useRef(false);

  useEffect(() => {
    if (!actx) return;
    console.log('actx', actx);
    // func take actx and return a buffer
    let absn;
    // const source = actx.createBufferSource();
    const fetchAudio = async () => {
      const arrayBuffer = await fetch(track.path[track.currPlay]).then((res) =>
        res.arrayBuffer()
      );
      const buffer = await actx.decodeAudioData(arrayBuffer);
      return buffer;
    };

    fetchAudio()
      .then((buffer) => {
        absn = new AudioBufferSourceNode(actx, { buffer, loop: true });
        console.log('absn', absn);
      })
      .catch((err) => console.error(err));
    absn.connect(actx.destination);
    dispatch(addAudioTrack(absn));
    audioTracks.push(absn);

    return () => {
      absn.stop();
      absn.disconnect();
    };
    // audioTracks[track.id].current.loop = true;
    // audioTracks[track.id].volume = track.volume;
    // audioTracks[track.id].muted = track.isMute;

    // audioTracks[track.id] = new Gapless5({
    //   useHTML5Audio: false,
    //   tracks: track.path,
    //   singleMode: true,
    //   loop: true,
    //   volume: track.volume,
    //   muted: track.isMute,
    // });

    // const src = actx?.createMediaElementSource(audioTracks[track.id]);
    // src.connect(gainNode).connect(actx.destination);
    // if (actx?.state === 'suspended') {
    //   actx.resume();
    // }
    // tracks.forEach((track) => {
    //   track.audio = new Gapless5({
    //     tracks: track.path,
    //     singleMode: true,
    //     loop: true,
    //     volume: track.volume,
    //     useHTML5Audio: false,
    //   });
    // });
  }, []);

  useEffect(() => {
    // create new buffer of new track
  }, [track.currPlay]);

  useEffect(() => {
    if (track.isMute) {
      //set volume to 0
    } else {
      // set volume to track.volume
    }
  }, [track.volume, track.isMute]);

  const createAudioTrack = (buffer, audioRef, gainRef) => {
    return {
      id: track.id,
      buffer,
      audioRef,
      gainRef,
    };
  };

  const toggleActive = () => {
    const audioTrack = audioTracks.find(
      (audioTrack) => audioTrack.id === track.id
    );
    if (track.isActive) {
      audioTrack.audioRef.current.volume = 0;
    } else {
      audioTrack.audioRef.current.volume = track.volume;
    }
    dispatch(toggleTrackActive(track));
  };

  const switchNextTrack = () => {
    if (track.currPlay + 1 === track.path.length) {
      audioTracks[track.id].current.queueTrack(0);
    } else {
      audioTracks[track.id].current.queueTrack(track.currPlay + 1);
    }
    dispatch(switchTrack(track));
  };

  const setVolume = (value) => {
    setGain(gainNode.gain.value);
    audioTracks[track.id].current.setVolume(value / 100);
    track.volume = value / 100;
    dispatch(setTrackVolume(track, value));
  };

  return (
    <div
      className="pad flex column br8"
      data-active={track.isActive}
      style={{ backgroundColor: track.isActive ? `${track.color}` : '#444444' }}
    >
      <div className="top flex">
        <div className="icon flex align-center">
          {track.currPlay + 1} / {track.path.length}
        </div>
        <div className="title flex align-center" {...provided.dragHandleProps}>
          {track.title}
        </div>
        <div className="pad-controls flex align-center">
          <button onClick={switchNextTrack}>
            <Replace />
          </button>
          <Toggle isActive={track.isActive} toggleActive={toggleActive} />
        </div>
      </div>
      <Slider track={track} setVolume={setVolume} />
    </div>
  );
};
